import logo from './../images/logo.svg';
import './../index.css';

function Header() {
  return(
    <header className="header">
      <button 
      href="#" 
      target="_blank"
      className="header__logo">
        <img   
        src={logo}
        alt="Лого: Mesto" />
        </button>
   </header>
  )
}

export default Header;