import React from 'react';

import api from '../utils/api';
import './../index.css';

import Header from './Header';
import Main from './Main';
import Footer from './Footer';

import Card from './Card';

import PopupWithForm from './PopupWithForm';
import ImagePopup from './ImagePopup';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';

import CurrentUserContext from '../contexts/CurrentUserContext';
import CurrentCardsContext from '../contexts/CurrentCardsContext';

function App() {

  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);

  const [selectedCard, setSelectedCard] = React.useState();

  const [currentUser, setCurrentUser] = React.useState([]);
  
  const [cards, setCards] = React.useState([]);

  React.useEffect(() => {
    api.getUserInformation()
    .then((res) => {
      setCurrentUser(res);
    })
    .catch(err => console.log('Ошибка загрузки данных пользователя'));
  }, [])

  React.useEffect(() => {
    api.getInitialCards()
    .then((res) => {
      setCards(res.map((item) => (
        <Card
          key={item._id}
          id={item._id}
          selectImage={handleCardClick}
          card={item}
          like={handleCardLike}
          delete={handleCardDelete}
        />
      )))
    })
    .catch(err => console.log('Ошибка загрузки карточек'));
  }, [cards])

  function handleCardLike(card) {
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    
    api.changeLikeCardStatus(card._id, !isLiked)
    .then((newCard) => {
      const newCards = cards.map((c) => c._id === card._id ? newCard : c);
      setCards(newCards);
    })
    .catch(err => console.log('Ошибка лайка'));
  } 

  function handleCardDelete(card) {
    api.deleteCard(card._id)
    .then(() => {
      const newCards = cards.filter((c) => c._id !== card._id);
      setCards(newCards);
    })
    .catch(err => console.log('Ошибка при удалении карточки'));
  }


  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true);
  }

  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true);
  }

  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(true);
 }

  function closeAllPopups() {
    setIsEditAvatarPopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setIsAddPlacePopupOpen(false);

    setSelectedCard(null)
  }

  function handleCardClick(card) {
    setSelectedCard(card);
  }

  function handleUpdateUser({name, about}) {
    api.changeUserInformation(name, about)
    .then((res) => {
      setCurrentUser(res);
      setIsEditProfilePopupOpen(false);
    })
    .catch(err => console.log('Ошибка при изменении данных пользователя'));
  }

  function handleUpdateAvatar({avatar}) {
    api.changeUserAvatar(avatar)
    .then((res) => {
      setCurrentUser(res);
      setIsEditAvatarPopupOpen(false);
    })
    .catch(err => console.log('Ошибка при изменении аватара пользователя'));
  }

  function handleAddPlaceSubmit({name, link}) {
    api.addNewCard({name, link})
    .then(() => {
      setIsAddPlacePopupOpen(false);
    })
    .catch(err => console.log('Ошибка при добавлении новой карточки'));
  }

  
  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page"> 

        <div className="page__container"> 

          <Header />
          <CurrentCardsContext.Provider value={cards}>
            <Main 
              onEditAvatar={handleEditAvatarClick}
              onEditProfile={handleEditProfileClick}
              onAddPlace={handleAddPlaceClick}
            />
          </CurrentCardsContext.Provider>
          <Footer />
          
          
          {/* попапы */}

          {/* «Редактировать профиль» */}
          <EditProfilePopup 
          isOpen={isEditProfilePopupOpen} 
          onClose={closeAllPopups} 
          onUpdateUser={handleUpdateUser}
          /> 

          {/* «Новое место» */}
          <AddPlacePopup 
          isOpen={isAddPlacePopupOpen} 
          onClose={closeAllPopups} 
          onAddPlace={handleAddPlaceSubmit}
          /> 

          {/* «Обновить аватар» */}
          <EditAvatarPopup 
          isOpen={isEditAvatarPopupOpen} 
          onClose={closeAllPopups} 
          onUpdateAvatar={handleUpdateAvatar}
          /> 

          {/* «Вы уверены?» */}
          <PopupWithForm 
          name="confirm" 
          title="Вы уверены?" 
          isOpen="" 
          onClose={closeAllPopups}/>

          {/* Открытие картинки */}
          <ImagePopup
          card={selectedCard}
          isOpen={selectedCard}
          onClose={closeAllPopups}
          />

        </div>

      </div>
    </CurrentUserContext.Provider>
  );
}

export default App;
