import React from 'react';
import PopupWithForm from './PopupWithForm';
import CurrentUserContext from './../contexts/CurrentUserContext'

function EditAvatarPopup(props) {
  const currentUser = React.useContext(CurrentUserContext);
  const avatarRef = React.useRef('');

  React.useEffect(() => {
    avatarRef.current.src = currentUser.avatar;
  }, [currentUser]); 

  function handleSubmit(e) {
    e.preventDefault();
  
    props.onUpdateAvatar({
      avatar: avatarRef.current.src,
    });
  } 

  function avatarChange(e) {
    avatarRef.current.src = e.target.value;
  }


  return(
    <PopupWithForm 
    name="avatar" 
    title="Обновить аватар" 
    isOpen={props.isOpen} 
    onClose={props.onClose}
    onSubmit={handleSubmit}
    >
      <div className="popup__inputs popup__inputs_tiny">
  
        <input 
        id="avatar-link-input" 
        type="url" 
        className="popup__input popup__input_type_avatar" 
        name="link" 
        placeholder="Ссылка на картинку" 
        ref={avatarRef}
        onChange={avatarChange}
        required />
        <span id="avatar-link-input-error" className="popup__error"></span>
  
      </div>
    </PopupWithForm>
  )
}

export default EditAvatarPopup;